s if needed and specify how you want to target the specific media.\n * Check out the examples for more info.\n * By default, the Sugar toolkit offer these medias to be targeted:\n * - mobile\n * - mobile-portrait\n * - mobile-landscape\n * - tablet\n * - tablet-portrait\n * - tablet-landscape\n * - desktop\n *\n * You can use some \"actions\" to specify your target. The available actions are:\n * - '>': Greater than...\n * - '<': Lower than...\n * - '=': Equal...\n * - '>=': Greater or equal...\n * - '>=': Lower or equal...\n *\n * Your media queries can be something like this: '<tablet', '>mobile <desktop', '=tablet-landscape'.\n * If you don't specicy any action in your queries, by default the \"defaultAction\" property in the media.config.js file will\n * be applied and this default action is setted to \">=\". This is setted like this to work in \"mobile first\" schema by default.\n *\n * @param       {String}        $media      The media(s) you want to target.\n *\n * @example       scss\n * .my-cool-container {\n *    background: red;\n *\n *    @include Sugar.media(tablet) {\n *      background: blue;\n *    }\n *\n *    @include Sugar.media('>=desktop') {\n *      background: green;\n *    }\n * }\n *\n * @since       2.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name            sr-only\n * @namespace       scss.screen-reader\n * @type            Mixin\n *\n * Make the element visible only for the screen readers\n *\n * @since       2.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @todo        Mixin header and documentation\n */\n/**\n * @todo        Mixin header and documentation\n */\n/**\n * @todo          Mixin header and documentation\n */\n/**\n * @name      round-overflow\n * @namespace           scss.shape\n * @type      mixin\n *\n * Apply some css to have a rounded element with already an overflow on it\n *\n * @param \t\t{Number} \t\t[$size=null] \t\tThe size (width and height) of the element wanted\n * @param \t\t{Number} \t\t[$width=null] \t\tThe width of the element wanted\n * @param \t\t{Number} \t\t[$height=null] \t\tThe height of the element wanted\n *\n * @example \tscss\n * .my-cool-image {\n * \t\\@include Sugar.round-overflow(100px);\n *\n * \timg {\n * \t\t\\@include Sugar.size(cover);\n * \t}\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name        triangle\n * @namespace           scss.shape\n * @type      mixin\n *\n * Generate a triangle shape\n *\n * @param       {Number}        [$width=1em]              The triangle width\n * @param       {Number}        [$height=1em]             The triangle height\n * @param       {Color}         [$color=color(primary)]    The triangle color\n * @param       {String}        [$direction=right]        The triangle direction. Can be top, right, bottom, left, top-left, top-right, bottom-left or bottom-right\n * @param       {String}        [$transform-origin=center]    The triangle transform origin. Can be center or base\n * @param       {Number}        [$border-width=0]         The border width\n * @param       {Color}         [$border-color=null]      The border color\n *\n * @example     scss\n * .my-cool-triangle {\n *    @include Sugar.triangle();\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      rhythm-width\n * @namespace           scss.typography\n * @type      function\n *\n * Return the Map properties representation for the rhythme width\n * The rhythme width is the width of an element that target a certain number of letters by line\n *\n * @param \t\t\t{Integer} \t\t\t[$letters-count=settings.typography.line-letters-count] \t\tThe number of letters to target by line\n * @return \t\t\t{Map} \t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\tThe Map properties representation\n *\n * @example     scss\n * Sugar.rhythm-width(50);\n * // return\n * // (\n * // \tdisplay : block,\n * // \tmax-width : 50ex\n * // )\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name        rhythm-width\n * @namespace           scss.typography\n * @type      mixin\n *\n * Print out the css on a container that will make the lines displays a maximum number of characters.\n *\n * @example \tscss\n * h1 {\n * \t\\@include Sugar.rhythm-width(45);\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    selection\n * @namespace           scss.typography\n * @type      mixin\n *\n * Set the user selection styling with ease\n *\n * @example \tscss\n * \\@include Sugar.selection() {\n * \tbackground: red;\n * \tcolor: white;\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      text-hide\n * @namespace           scss.typography\n * @type      mixin\n *\n * Hide the contained text\n *\n * @example \tscss\n * .my-cool-container {\n * \t\\@include Sugar.text-hide();\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      text-truncate\n * @namespace           scss.typography\n * @type      mixin\n *\n * Print out the style to truncate the text if needed\n *\n * @example \tscss\n * h1 {\n * \t\\@include Sugar.text-truncate();\n * }\n *\n * @since      1.0.0\n * @author \t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      convert\n * @namespace           scss.unit\n * @type      function\n *\n * Convert a passed value to the wanted unit\n * The conversion between some units that depends on a font-size will take the settings.typography.font-size value as reference.\n *\n * Available conversions\n * - from px\n * \t- to em\n * \t- to rem\n * \t- to pt\n * \t- to %\n *\n * - from em\n * \t- to rem\n * \t- to px\n * \t- to pt\n * \t- to %\n *\n * - from pt\n * \t- to em\n * \t- to rem\n * \t- to px\n * \t- to pt\n * \t- to %\n *\n * - from rem\n * \t- to em\n * \t- to px\n * \t- to pt\n * \t- to %\n *\n * @param \t\t\t{Number} \t\t$value \t\t\tThe value to convert\n * @param \t\t\t{String} \t\t$unit \t\t\tThe unit in which to convert the value\n * @return \t\t\t{Number} \t\t\t\t\t\tThe converted value\n *\n * @example     scss\n * // if the settings.typography.font-size === 16px\n * Sugar.convert(24px, rem) // 1.5rem\n * Sugar.convert(3rem, pt) // 36pt\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      is-unitless\n * @namespace           scss.unit\n * @type      function\n *\n * Check if the passed variable is unitless or not\n *\n * @param         {Number}        $number       The number to check\n * @return        {Boolean}                     true if unitless, false if not\n *\n * @example       scss\n * Sugar.is-unitless(20); // => true\n * Sugar.is-unitless(10px); // => false\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    strip-units\n * @namespace           scss.unit\n * @type      function\n *\n * Return a number without any units\n *\n * @param \t\t\t{Number} \t\t$number \t\tThe number to process\n * @return \t\t\t{Number} \t\t\t\t\t\tThe number without units\n *\n * @example       scss\n * Sugar.strip-units(12px); // => 12\n * Sugar.strip-units(30rem); // => 30\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    to-em\n * @namespace           scss.unit\n * @type      function\n *\n * Convert the passed value to em\n *\n * @param \t\t\t{Number} \t\t$value \t\tThe value to convert\n * @return \t\t\t{Number} \t\t\t\t\tThe em converted value\n *\n * @example     scss\n * Sugar.to-em(10px);\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    to-percent\n * @namespace           scss.unit\n * @type      function\n *\n * Convert the passed value to %\n *\n * @param \t\t\t{Number} \t\t$value \t\tThe value to convert\n * @return \t\t\t{Number} \t\t\t\t\tThe % converted value\n *\n * @example     scss\n * Sugar.to-percent(10px);\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      to-pt\n * @namespace           scss.unit\n * @type      function\n *\n * Convert the passed value to pt\n *\n * @param \t\t\t{Number} \t\t$value \t\tThe value to convert\n * @return \t\t\t{Number} \t\t\t\t\tThe pt converted value\n *\n * @example       scss\n * Sugar.to-pt(10px);\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    to-px\n * @namespace           scss.unit\n * @type      function\n *\n * Convert the passed value to px\n *\n * @param \t\t\t{Number} \t\t$value \t\tThe value to convert\n * @return \t\t\t{Number} \t\t\t\t\tThe px converted value\n *\n * @example       scss\n * Sugar.to-px(10rem);\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    to-rem\n * @namespace           scss.unit\n * @type      function\n *\n * Convert the passed value to rem\n *\n * @param \t\t\t{Number} \t\t$value \t\tThe value to convert\n * @return \t\t\t{Number} \t\t\t\t\tThe rem converted value\n *\n * @example     scss\n * Sugar.to-rem(10px);\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    unit\n * @namespace           scss.unit\n * @type      function\n *\n * Return the unit of the passed number\n *\n * @param       {Number}      $number       The number to get the unit from\n * @return      {String}                    The unit\n *\n * @example     scss\n * Sugar.unit(10px); // => px\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name    border-map\n * @namespace           scss.util\n * @type      function\n *\n * Parse and return the Map respresentation of a border property\n * The $border argument will be parsed with the [s-parse-properties](../core/functions/_s-parse-properties.scss) function.\n *\n * @param \t\t{List} \t\t\t$border \t\t\tThe border property to parse\n * @return \t\t{Map} \t\t\t\t\t\t\t\tThe Map represenration of the border\n *\n * @example       scss\n * Sugar.border-map(1px solid white);\n * // return\n * // (\n * // \twidth : 1px,\n * // \tcolor : white,\n * // \tstyle : solid\n * // )\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      margin-map\n * @namespace           scss.util\n * @type      function\n *\n * Parse and return the Map respresentation of a margin property\n * The $margin argument will be parsed with the [s-parse-properties](../core/functions/_s-parse-properties.scss) function.\n *\n * @param \t\t\t{List|Number} \t\t\t$margin \t\t\tThe margin property to parse\n * @return \t\t\t{Map} \t\t\t\t\t\t\t\t\t\tThe map representation\n *\n * @example   scss\n * Sugar.margin-map(10px); // => ( top:10px, right:10px, bottom:10px, left:10px )\n * Sugar.margin-map(10px 20px); // => ( top:10px, right:20px, bottom:10px, left:20px )\n * Sugar.margin-map(5px 10px 15px 20px); // => ( top:5px, right:10px, bottom:15px, left:20px )\n *\n * @since      1.0.0\n * @author \t\t\tOlivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)\n */\n/**\n * @name      position-map\n * @namespace           scss.util\n * @type      function